.paginate {
  width: 58%;
  margin: 40px auto 0;
}

.paginate .page-link {
  width: 62px;
  height: 45px;
  margin-bottom: -3px;
  background-color: #faeddf;
  color: #000;
  padding: 10px 26px;
  font-weight: 600;
  border-radius: 5px;
  border: 1px solid #0e5540;
}
.paginate .page-link.active {
  color: #faeddf;
  background-color: #0e5540;
}
